
import { defineComponent, onMounted, ref } from "vue";
import { useRouter, useRoute } from "vue-router";
import IConcessionary from "@/store/modules/concessionary/interface/concessionary.interface"
import ISite from "@/store/modules/site/interface/site.interface"
import ConcessionaryService from "@/store/modules/concessionary/service/concessionary.service";
import SiteService from "@/store/modules/site/service/site.service";
import Swal from "sweetalert2/dist/sweetalert2.js";
import Multiselect from '@vueform/multiselect';
import { createToaster } from "@meforma/vue-toaster";
import SettingService from "@/store/modules/setting/service/setting.service";
import ISetting from "@/store/modules/setting/interface/setting.interface";
import moment from "moment";
export default defineComponent({
  name: "Concessionaries",
  components: {
	  Multiselect
  },
  setup() {
    	const router = useRouter();
		const show = ref<Boolean>(false);
		const showDefault = ref<Boolean>(false);
		const siteService = new SiteService();
		const concessionaryService = new ConcessionaryService();
		const sites = ref<Array<ISite>>([]);
		const concessionaries = ref<Array<any>>([]);
		const toaster = createToaster({ position: "bottom-left" });
		const concessionary = ref<IConcessionary>({
			id: null,
			name: ''
		});
		let siteName = ref<String>('');
		const messageDefault = ref<String>('');

		const setting = ref<ISetting>({
            id: null,
            description: '',
            value: '',
            slug: ''
        });
        const settingService = new SettingService();

        const getDataSetting = () => {
			showDefault.value = true;
			settingService.getDataConcessionary()
			.then((response) => {
				setting.value = response.data
				if(setting.value){
					messageDefault.value = setting.value.value;
				}
				else{
					messageDefault.value = '';
				}
				showDefault.value = false;
			})
			.catch((error) => {
				showDefault.value = false;
			});
		}

		const saveDataDefault = () => {
			let data = Object.assign(setting.value, {value: messageDefault.value});
			data.value = messageDefault.value;
			settingService.update(setting.value.id, data)
			.then((response) => {
				Swal.fire(
				'Confirmacin!',
				'Informacin actualizada',
				'success'
				)
				.then((result) => {
					getDataSetting()
					document.getElementById('message-close-button')?.click()
				});
			})
			.catch((error) => {
				Swal.fire(
				'Oh no!',
				'No se guardaron los cambios',
				'error'
				)
			});
		}

		const getSites = () => siteService.getData()
		.then((response) => {
			sites.value = [...[{id: null, name: 'Todas las sedes'}], ...response.data]
		})
		.catch((error) => {
			
		});

		const getData = (sedeId) => concessionaryService.getDataBySede(sedeId)
		.then((response) => {
			concessionaries.value = response.data
		})
		.catch((error) => {
			
		});

		const editMessage = (concessionaryData: any) => {
			concessionary.value = Object.assign({}, concessionaryData);
		}

		const validateMessage = () => {
			let disabled = false;
			if(!concessionary.value.id || !concessionary.value['message'] || concessionary.value['message'] == '') disabled = true;
			return disabled;
		}

		const saveData = () => {
			Swal.fire({
				title: 'Confirmacin!',
				text: "Esta seguro que desea cambiar los cambios?",
				icon: 'question',
				showCancelButton: true,
				confirmButtonColor: '#3085d6',
				cancelButtonColor: '#d33',
				confirmButtonText: 'Si, Guardar',
				cancelButtonText: 'No, Cancelar',
				reverseButtons: true
				}).then((result) => {
					if (result.isConfirmed) {
						concessionaryService.update(concessionary.value.id, concessionary.value)
						.then((response) => {
							Swal.fire(
							'Confirmacin!',
							'Informacin actualizada',
							'success'
							)
							.then((result) => {
								getData(siteName.value)
								document.getElementById('animation-close-button')?.click()
							});
							
						})
						.catch((error) => {
							Swal.fire(
							'Oh no!',
							'No se guardaron los cambios',
							'error'
							)
						});
					}
			})
		}

		const changeSite = (e) => {
			if(e && e != ''){
				siteName.value = ''
				sites.value.forEach(site =>{
					if(site.id == e){
						siteName.value = site.name
					}
				})
				getData(siteName.value)
			}
			else{
				concessionaries.value = []
			}
		}

		const editMessageDefault = () => {
			getDataSetting();
		}

		onMounted(() => {
			getDataSetting();
			getSites();
		});

		return {
			concessionaries,
			concessionary,
			siteName,
			sites,
			validateMessage,
			saveData,
			changeSite,
			editMessage,
			messageDefault,
			editMessageDefault,
			setting,
			saveDataDefault
		};
  },
});
